.submitButton {
  max-width: 370px;
  width: 100%;
  background: #06599F;
  padding: 25px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  margin: 20px 0 20px 0;
}
.submitButton:disabled {
  opacity: 0.5;
}
@media (max-width: 768px) {
  .submitButton {
    font-size: 18px;
    max-width: 100%;
    padding: 20px;
  }
}
header {
  width: 100%;
}
header .header {
  z-index: 9999;
  position: fixed;
  width: 100%;
  height: 110px;
  font-size: 24px;
  display: flex;
  align-items: center;
  background: #ffffff;
  box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.5);
}
header .header .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 768px) {
  header .header .row {
    justify-content: normal;
  }
}
header .header .burger {
  display: none;
}
@media (max-width: 768px) {
  header .header .burger {
    margin-right: 15px;
    position: relative;
    width: 40px;
    height: 25px;
    display: flex;
    align-items: center;
  }
  header .header .burger span {
    width: 100%;
    height: 5px;
    background: #06599F;
  }
  header .header .burger:before {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    top: 0;
    height: 5px;
    background: #06599F;
  }
  header .header .burger:after {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    bottom: 0;
    height: 5px;
    background: #06599F;
  }
}
header .header .logo {
  display: block;
  max-width: 265px;
  width: 100%;
}
@media (max-width: 768px) {
  header .header .logo {
    width: 150px;
  }
}
header .header .logoWhite :global .st5 {
  fill: white;
}
header .header .close {
  max-width: 845px;
  width: 100%;
  margin-left: 50px;
}
@media (max-width: 768px) {
  header .header .close {
    margin-left: 0;
    position: fixed;
    left: 0;
    top: 0;
    max-width: 0;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
  }
}
header .header .close nav.active {
  left: 0;
}
header .header .close nav {
  max-width: 845px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  color: #06599F;
  font-weight: bold;
}
header .header .close nav div {
  margin-right: 15px;
}
header .header .close nav div:last-child {
  margin-right: 0;
}
@media (max-width: 992px) {
  header .header .close nav div:last-child {
    display: none;
  }
}
@media (max-width: 768px) {
  header .header .close nav div:last-child {
    display: block;
  }
}
header .header .close nav div:first-child {
  display: none;
}
header .header .close nav a {
  color: #06599F;
  padding: 15px;
}
@media (max-width: 768px) {
  header .header .close nav {
    transition: left 300ms;
    width: 100%;
    position: fixed;
    max-width: 250px;
    overflow: hidden;
    flex-direction: column;
    align-items: normal;
    justify-content: normal;
    top: 0;
    left: -250px;
    height: 100%;
    background: #06599F;
    color: white;
    white-space: nowrap;
  }
  header .header .close nav div {
    margin-right: 0;
    padding: 15px;
  }
  header .header .close nav div:first-child {
    display: block;
    width: 150px;
  }
  header .header .close nav a {
    margin-right: 0;
    color: white;
  }
}
@media (max-width: 768px) {
  header .header .close.active {
    max-width: 100%;
  }
}
header .isActive {
  background: #68a0ff;
  border-radius: 5px;
}
@media (max-width: 768px) {
  header .isActive {
    border-radius: 0;
  }
}
header .headerBlue {
  background: linear-gradient(90deg, #06599f 57%, rgba(6, 89, 159, 0.50463936) 100%);
}
header .headerBlue a,
header .headerBlue div {
  color: white !important;
}
header .headerBlue .burger:before,
header .headerBlue .burger:after,
header .headerBlue .burger span {
  background: white;
}
@media (max-width: 768px) {
  header .header {
    height: 60px;
    font-size: 20px;
  }
}
