.submitButton {
  max-width: 370px;
  width: 100%;
  background: #06599F;
  padding: 25px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  margin: 20px 0 20px 0;
}
.submitButton:disabled {
  opacity: 0.5;
}
@media (max-width: 768px) {
  .submitButton {
    font-size: 18px;
    max-width: 100%;
    padding: 20px;
  }
}
.register {
  padding-top: 110px;
  display: flex;
  height: 100%;
  background: url(../../../imgs/fon.svg) no-repeat center center;
  background-size: cover;
  flex: 1 1 auto;
  flex-direction: column;
}
@media (max-width: 768px) {
  .register {
    padding-top: 60px;
  }
}
.register .container {
  display: flex;
  justify-content: center;
}
.register .body {
  margin-bottom: 100px;
  max-width: 600px;
  width: 100%;
  padding: 15px;
  backdrop-filter: blur(8px);
  border: 1px solid #d9d9d9;
  border-radius: 5px;
}
.register .body form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.register .body label {
  font-size: 18px;
  font-weight: 500;
  color: #06599F;
  width: 100%;
}
.register .body :global .ant-input-affix-wrapper {
  margin: 15px 0 20px 0;
  padding: 10px 16px;
}
.register .body .icon {
  margin-right: 10px;
}
.register .body .errorText {
  color: darkred;
  font-weight: normal;
}
.register .body .error {
  box-shadow: 0 0 0 2px rgba(255, 0, 0, 0.2) !important;
  border-color: darkred !important;
}
.register .body .error:focus {
  box-shadow: 0 0 0 2px rgba(255, 0, 0, 0.2);
  border-color: darkred;
}
.register .body input {
  margin: 15px 0 20px 0;
}
.register .body span input {
  margin: 0;
}
