.submitButton {
  max-width: 370px;
  width: 100%;
  background: #06599F;
  padding: 25px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  margin: 20px 0 20px 0;
}
.submitButton:disabled {
  opacity: 0.5;
}
@media (max-width: 768px) {
  .submitButton {
    font-size: 18px;
    max-width: 100%;
    padding: 20px;
  }
}
.login {
  position: relative;
  padding-top: 110px;
  display: flex;
  flex: 1 1 auto;
  min-height: 100vh;
  flex-direction: column;
  background: url(../../../imgs/fon.svg) no-repeat center center;
  background-size: cover;
}
.login img {
  position: absolute;
  right: 10%;
  bottom: 0;
  height: 60%;
}
@media (max-width: 768px) {
  .login img {
    display: none;
  }
}
@media (max-width: 768px) {
  .login {
    padding-top: 60px;
  }
}
.login .container {
  display: flex;
  justify-content: center;
}
.login .body {
  z-index: 2;
  margin-bottom: 100px;
  max-width: 600px;
  width: 100%;
  padding: 15px;
  backdrop-filter: blur(8px);
  border: 1px solid #d9d9d9;
  border-radius: 5px;
}
.login .body form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login .body label {
  font-size: 18px;
  font-weight: 500;
  color: #06599F;
  width: 100%;
}
.login .body input {
  margin-bottom: 15px;
}
